import React, { useContext } from "react";
import { useGoogleLogin } from "react-google-login";
import GoogleButton from "react-google-button";
import { useHistory } from "react-router-dom";
//Context
import { UpdateUserContext, UpdateUserNameContext, UpdateUserImageContext, UpdateUserEmailContext, UpdateUserPrivilegeContext, UserPrivilegeContext, UpdateErrorStatusContext } from "components/GeneralContextProvider/GeneralContextProvider"
import { refreshTokenSetup } from "variables/refreshTokenSetup";
//AppUser repository
import { getAppUserPrivilege } from "repositories/AppUserRepository"

const clientId = "816786373130-qia4ks1egpir1hmq1s3n3ks61blvcr6g.apps.googleusercontent.com"

function Login() {

  const setUserToken = useContext(UpdateUserContext);
  const setUserName = useContext(UpdateUserNameContext);
  const setUserEmail = useContext(UpdateUserEmailContext);
  const setUserImage = useContext(UpdateUserImageContext);
  const setUserPrivilege = useContext(UpdateUserPrivilegeContext);
  const setErrorStatus = useContext(UpdateErrorStatusContext);

  const userPrivilege = useContext(UserPrivilegeContext);

  let history = useHistory();

  //On successful authentication, set the relevant privileges to the user
  const onSuccess = (res) => {
    //Search for the user in the database
    let body = { email: res.profileObj.email }
    let userToken = res.tokenId;
    getAppUserPrivilege(body, setUserPrivilege, onQueryError, userToken)
    if(Number(userPrivilege) >= 0) {
      refreshTokenSetup(res, setUserToken);
      setUserToken(res.tokenId);
      setUserName(res.profileObj.name);
      setUserEmail(res.profileObj.email);
      setUserImage(res.profileObj.imageUrl);
      console.log("Login Success !");
      history.push("/admin/dashboard");
    } else {
      alert("Login failed !");
      console.log("Login Failed !");
    }  
  };

  const onFailure = (res) => {
    console.log("Login failed res:", res);
  };

  const { signIn } = useGoogleLogin({
    onSuccess,
    onFailure,
    clientId,
    //isSignedIn: true,
    accessType: "offline",
  });

  function onQueryError(error) {
    let statusCode = parseInt(error.message);
    setErrorStatus(true);
  }

  return <GoogleButton label="Iniciar sesión con Google" onClick={signIn} />;
}

export default Login;
